<template>
    <div class="commoidy">
        <action-bar title="添加商品"></action-bar>
        <div class="content">
          <scroll-view class="scroll" :data="goodsList">
            <goodsItem v-for="item in goodsList" :key="item.id">
                <van-image slot="imgSlot" :src="baseUrl + item.imageUrl" class="goods-img" />
                <div slot="nameSlot">
                    <div>{{ item.skuName }}</div>
                </div>
                <div slot="attributeSlot">
                    <div class="attribute">
                        <div class="attribute-content">分类：{{ item.categoryName }}</div>
                        <div class="attribute-content">颜色：{{ item.color }}</div>
                    </div>
                    <div class="attribute">
                        <div class="attribute-content">等级：{{ item.level }}</div>
                        <div class="attribute-content">规格：{{ item.unit }}</div>
                    </div>
                </div>
            </goodsItem>
        </scroll-view>
        </div>
    </div>
</template>
<script>
import goodsItem from '@/components/util/GoodsItem.vue'
import { libraryInfo } from '@/http/api'

export default {
  name: 'alreadyAddCommoidy',
  components: {
    goodsItem
  },
  computed: {
    baseUrl () {
      return this.$store.state.baseUrl
    }
  },
  data () {
    return {
      goodsList: []
    }
  },
  methods: {
    getList () {
      libraryInfo().then(res => {
        this.goodsList = res.data
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
    this.getList()
  }
}
</script>
<style lang="scss" scoped>
.commoidy {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
}

.content{
  flex-grow: 1;
  position: relative;
}

.scroll {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.goods-img {
  width: 20vw;
  height: 20vw;
  border-radius: .25rem;
  background-color: #F2F2F2;
  margin-right: 10rpx;
}

.attribute {
  display: flex;
  flex-direction: row;
  margin-top: .5rem;
}

.attribute-content {
  width: 50%;
}

</style>
